import { h, Component } from 'preact';
import map from 'lodash.map';
import { firestore } from '../fire';
import Article from '../article';
// import style from './style';

export default class ArticleList extends Component {
	constructor(props) {
		super(props);
		this.state = {
			articles: [
				// {
				// 	createdOn: '2017-12-09T23:49:41.778Z',
				// 	feedsrc: 'Echo JS',
				// 	labels: [],
				// 	link: 'https://is.gd/9ouvi9',
				// 	opengraph: {},
				// 	sentiment: {},
				// 	title: 'React newsletter: 68'
				// }
			]
		};
	}
	componentWillMount() {
		console.log('firestore:', firestore);
		// firestore
		// 	.collection('publicArticles')
		// 	.orderBy('createdOn')
		// 	.limit(50)
		// 	.get()
		// 	.then(snapshot => {
		// 		const arts = [];
		// 		snapshot.forEach(doc => arts.push(doc.data()));
		// 		this.setState({ articles: arts });
		// 	});
		firestore
			.collection('publicArticles')
			.orderBy('createdOn', 'asc')
			// .limit(50)
			.onSnapshot({ includeQueryMetadataChanges: true }, snapshot => {
				const arts = this.state.articles;
				console.log('snapshot:', snapshot);
				snapshot.docChanges.forEach(change => {
					console.log('change:', change);
					// if (change.type === 'added') {
					// 	console.log('new article: ', change.doc.data());
					// }
					let source = snapshot.metadata.fromCache ? 'local cache' : 'server';
					console.log('data came from ' + source);
					arts.unshift(change.doc.data());
				});
				this.setState({ articles: arts });
			});
	}

	render(props, state) {
		return (
			<section>{map(state.articles, (article, key) => <Article key={key} {...article} />)}</section>
		);
	}
}



